.session-summary-page {
  padding: 20px;
  max-width: 600px;
  margin: auto;
  color: #333;
}

.summary-card {
  background: #f3f6ff;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 0 10px rgba(0,0,0,0.05);
}

.summary-card .notes {
  margin-top: 10px;
  white-space: pre-wrap;
}