.spline-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  transition: all 0.5s ease-in-out;
  z-index: 10;
  pointer-events: none;
  opacity: 1;
}

.spline-wrapper.shrink {
  width: 100%;
  height: 100%;
  opacity: 0.15;
  z-index: -1;
  filter: blur(2px);
}
